import { SearchOutlined } from "@ant-design/icons";
import { AutoComplete, Button, DatePicker, Input } from "antd";
import { DATE_FORMAT, RESET, SEARCH } from "constants/CommonConstants";
import { uniqBy, uniqueId } from "lodash";
import { forwardRef, useEffect, useState } from "react";
import Utils from "utils";

const SearchFilterDropdown = forwardRef((props, ref) => {
  const [options, setOptions] = useState([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [data, setData] = useState([]);

  const {
    setSelectedKeys,
    selectedKeys,
    confirm,
    clearFilters,
    handleSearch,
    dataIndex,
    handleReset,
    label = null,
    inputId = "",
    resetBtnId = "",
    searchBtnId = "",
    dataSource,
    field,
  } = props;

  useEffect(() => {
    if (dataSource && dataSource.length > 0) {
      setData(dataSource);
    }
  }, [dataSource]);

  const onChange = dataIndex => value => {
    setSelectedKeys(value ? [value] : []);
    setSearchTerm(value);
    if (!Utils.isEmpty(value)) {
      const options = Utils.sortArray(
        data?.map(record => ({ key: uniqueId(), value: record[dataIndex] || "" })),
        "value",
      );
      setOptions(uniqBy(options, "value"));
    } else {
      setOptions([]);
    }
  };

  const onReset = () => {
    handleReset(clearFilters, confirm);
    setSearchTerm("");
    setSelectedKeys([""]);
    setOptions([]);
  };

  return (
    <div className="p-2">
      {field && field.type.toLowerCase() === "date" ? (
        <DatePicker
          format={DATE_FORMAT}
          value={selectedKeys[0]}
          onChange={e => {
            setSelectedKeys([e]);
          }}
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          className="mb-2 d-block"
          allowClear={true}
          ref={node => (ref = node)}
        />
      ) : (
        <AutoComplete
          ref={node => {
            ref = node;
          }}
          placeholder={`Search ${label || dataIndex}`}
          value={searchTerm}
          onChange={onChange(dataIndex)}
          onSelect={() => handleSearch(selectedKeys, confirm, dataIndex)}
          className="mb-2 d-block"
          autoFocus
          defaultOpen={false}
          id={inputId}
          filterOption={true}
          options={options}
        >
          <Input
            onPressEnter={()=> handleSearch(selectedKeys, confirm, dataIndex)}
          />
        </AutoComplete>
      )}
      <Button className="mr-2" onClick={onReset} size="small" id={resetBtnId}>
        {RESET.text}
      </Button>
      <Button
        type="primary"
        onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
        icon={<SearchOutlined className="mr-1" />}
        size="small"
        id={searchBtnId}
      >
        {SEARCH.text}
      </Button>
    </div>
  );
});

