import { Table } from "antd";
import { useState } from "react";

const TableComponent = props => {
  const {
    tableKey,
    columns,
    data,
    scroll,
    visibleData,
    tableSize,
    rowKey,
    handleChange,
    loading,
    paginationHandler,
    pagination = true,
    showSorterTooltip = true,
    isBordered = false,
    rowClassName,
    nestedTable,
    rowSelection,
    className
  } = props;

  const [keys, setKeys] = useState([]);

  const onRow = ({ employeeEmail }) => keys.includes(employeeEmail) && { className: "expand-parent" };

  const onExpand = (expanded, { employeeEmail }) => {
    const expandedKeys = expanded ? keys.concat(employeeEmail) : keys.filter(k => k !== employeeEmail);
    setKeys(expandedKeys);
  };

  return (
    <div style={{ position: "relative" }} className={className || null} >
      <Table
        rowSelection={rowSelection || null}
        loading={loading}
        tableLayout="fixed"
        key={tableKey}
        bordered={isBordered}
        columns={columns}
        dataSource={data}
        rowKey={rowKey}
        pagination={pagination && { showSizeChanger: true, ...paginationHandler }}
        scroll={scroll}
        summary={data => visibleData && visibleData(data)}
        size={tableSize}
        onChange={handleChange}
        showSorterTooltip={showSorterTooltip}
        onRow={onRow}
        onExpand={onExpand}
        rowClassName={rowClassName}
        expandable={{
          expandedRowRender: nestedTable ? record => nestedTable(record) : null,
        }}
      />
    </div>
  );
};

export default TableComponent;
