import { SearchOutlined, SyncOutlined, FilterOutlined } from "@ant-design/icons";
import { Button, Col, PageHeader, Row, Tooltip } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import { DATE_FORMAT, LIGHT_NAVY_BLUE_COLOR, SORT_DIRECTIONS, STATUS } from "constants/CommonConstants";
import { PROJECT_ALLOCATION_TABLE } from "constants/MyProjectAllocations";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useEffect, useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import projectService from "services/ProjectService";
import Utils from "utils";
import ViewTeamMembers from "./ViewTeamMembers";

const MyProjectAllocations = () => {
  const dispatch = useDispatch();
  const getMessage = useIntlMessage();
  const [myProjectAllocationsData, setmyProjectAllocationsData] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [searchText, setSearchText] = useState("");
  const [searchedColumn, setsearchedColumn] = useState("");
  let searchInput = useRef(null);
  const [sortedInfo, setSortedInfo] = useState({
    columnKey: STATUS,
    order: "",
  });
  const [selectedProject, setSelectedProject] = useState(null);
  const [showViewTeamModal, setShowViewTeamModal] = useState(false);

  useEffect(() => {
    fetchMyProjectAllocations();
  }, []);

  const handleFetchProjectAllocations = () => {
    fetchMyProjectAllocations();
    setSearchText("");
    setsearchedColumn("");
    setFilteredInfo({});
    setSortedInfo({});
  };

  const fetchMyProjectAllocations = async () => {
    try {
      dispatch(showLoader(true));
      const response = await projectService.allocationList();
      const updatedList = projectService.flatRecords(response);
      setmyProjectAllocationsData(updatedList);
    } catch (e) {
      console.error(e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const enableViewTeam = project => {
    setSelectedProject(project);
    setShowViewTeamModal(true);
  };

  const handleCancel = () => {
    setShowViewTeamModal(false);
    setSelectedProject(null);
  };
  const handleChange = (pagination, filters, sorter) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    setSearchText("");
    confirm();
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setsearchedColumn(dataIndex);
  };

  const getColumnSearchProps = field => {
    let filterObj = {
      filterIcon: filtered => (
        <SearchOutlined
          id={`manage-account-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      ),
    };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => (
        <FilterOutlined
          id={`manage-account-search-btn-${field.key}`}
          color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined}
        />
      );
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          field={field}
          handleSearch={handleSearch}
          dataIndex={field.key}
          searchInput={searchInput}
          handleReset={handleReset}
          label={getMessage(field.label)}
          dataSource={myProjectAllocationsData}
        />
      );

      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const valuee = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
        }
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const dataColumns = Object.values(PROJECT_ALLOCATION_TABLE).map(field => ({
    title: (
      <Tooltip title={getMessage(field.label)} placement="top">
        <span>{getMessage(field.label)}</span>
      </Tooltip>
    ),
    dataIndex: field.key,
    key: field.key,
    sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
    sortDirections: SORT_DIRECTIONS,
    filteredValue: filteredInfo[field.key] || null,
    sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
    ...getColumnSearchProps(field),
    render: (_, record) => {
      if (field.type === "date") {
        return projectService.prepareAllocationEndDate(field, record);
      }
      if (field.type === "select") {
        const selectedValueText = field?.options.find(x => x.value === record[field.key])?.label;
        return selectedValueText ? getMessage(selectedValueText) : "";
      }
      if (field.key === PROJECT_ALLOCATION_TABLE.PROJECT_NAME.key) {
        return (
          <span style={{ color: "#3f79f7", cursor: "pointer" }} onClick={() => enableViewTeam(record)}>
            {record[field.key]}
          </span>
        );
      }
      return record[field.key];
    },
  }));

  return (
    <>
      <Row align="middle" gutter={[20, 20]}>
        <Col xs={24} md={12} lg={12} xl={12}>
          <PageHeader
            subTitle={
              <Button className="ml-3">
                <SyncOutlined onClick={handleFetchProjectAllocations} shape="circle" />
              </Button>
            }
            className="p-2 pl-2"
            title={getMessage("sidenav.employee.myprojectallocations")}
          />
        </Col>
      </Row>
      <TableComponent
        rowKey={record => record.emailId}
        columns={[...dataColumns]}
        data={myProjectAllocationsData}
        scroll={{ x: 1300 }}
        handleChange={handleChange}
        tableSize="small"
        showDropdown={false}
        showSorterTooltip={false}
      />
      {showViewTeamModal && (
        <ViewTeamMembers selectedProject={selectedProject} handleCancel={handleCancel} visible={showViewTeamModal} />
      )}
    </>
  );
};

export default MyProjectAllocations;
