import { FilterOutlined, SearchOutlined } from "@ant-design/icons";
import { Col, Divider, Modal, PageHeader, Row, Table, Tooltip } from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import { ACTIVE_STATUS, LIGHT_NAVY_BLUE_COLOR, SORT_DIRECTIONS } from "constants/CommonConstants";
import { LABELS, TEAM_DETAILS_TABLE } from "constants/MyProjectAllocations";
import useIntlMessage from "hooks/useIntlMessage";
import { useEffect, useRef, useState } from "react";
import projectService from "services/ProjectService";
import Utils from "utils";

function ViewTeamMembers({ selectedProject, handleCancel, visible }) {
  let searchInput = useRef(null);
  const getMessage = useIntlMessage();
  const [resources, setResources] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [filteredInfo, setFilteredInfo] = useState({});
  const [isLoading, setIsLoading] = useState(false);

  const [sortedInfo, setSortedInfo] = useState({});
  const [searchedColumn, setSearchedColumn] = useState("");

  useEffect(() => {
    fetchProjectResources(selectedProject?.project?.projectId, ACTIVE_STATUS.value);
  }, [selectedProject]);

  const fetchProjectResources = async (projectId, currentStatus) => {
    try {
      setIsLoading(true);
      const projectResources = await projectService.getResourcesByProject(projectId, { status: currentStatus });
      setResources(projectResources);
    } catch (e) {
      console.error("Error in getting the project resource information", e);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex);
  };

  const handleChange = (_, filters, sorter, pagination) => {
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    setSearchText("");
    confirm();
  };

  const getColumnSearchProps = field => {
    let filterObj = { filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} /> };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => <FilterOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />;
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          searchInput={searchInput}
          handleReset={handleReset}
          label={getMessage(field.label)}
          dataSource={resources}
        />
      );
      filterObj.onFilter = (value, record) => {
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const dataColumns = Object.values(TEAM_DETAILS_TABLE).map(field => ({
    title: (
      <Tooltip title={getMessage(field.label)} placement="top">
        <span>{getMessage(field.label)}</span>
      </Tooltip>
    ),
    dataIndex: field.key,
    key: field.key,
    sorter: (a, b) => Utils.antdTableSorter(a, b, field.key),
    sortDirections: SORT_DIRECTIONS,
    filteredValue: filteredInfo[field.key] || null,
    sortOrder: sortedInfo.columnKey === field.key ? sortedInfo.order : null,
    ...getColumnSearchProps(field),
    render: (_, record) => {
      return record[field.key];
    },
  }));

  return (
    <Modal
      title={getMessage(LABELS.PROJECT_TITLE)}
      visible={visible}
      centered
      width="75%"
      footer={null}
      onCancel={handleCancel}
    >
      <Row align="middle" gutter={[20, 20]}>
        <Col xs={24} md={12} lg={12} xl={16}>
          <PageHeader
            className="p-2 pl-2"
            title={`${getMessage(LABELS.PROJECT_NAME)}:`}
            subTitle={selectedProject?.project?.projectName?.toUpperCase()}
          />
        </Col>
        <Col xs={24} md={12} lg={12} xl={8}>
          <PageHeader
            className="p-2 pl-2"
            title={`${getMessage(LABELS.CLIENT_DELIVERY_HEAD)}:`}
            subTitle={selectedProject?.clientDeliveryHead}
          />
        </Col>
      </Row>
      <Divider />
      <Table
        pagination={{ showSizeChanger: true }}
        rowKey={record => record.id}
        columns={dataColumns}
        dataSource={resources}
        showSorterTooltip={false}
        scroll={{ x: 1000, y: 470 }}
        onChange={handleChange}
        loading={isLoading}
      />
    </Modal>
  );
}

export default ViewTeamMembers;
